.container {
  text-align: left;
  margin: 3em .5em;
  opacity: 0.6;
  font-size: 0.8em;

  .unknown {
    color: #e44;
  }

  .url {
    max-width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .hash {
    margin-left: .2em;
  }

  .hash, .url {
    margin-bottom: -.2em;
    display: inline-block;
  }
}



