.frame {
  background: white;
  border: 0;
  position: absolute;
  height: 100%;
  width: 100%;
}

.full {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  background: white;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 300;

  .text {
    text-align: center;
    padding: 5em;
    font-size: 2em;
    color: #999;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}



